body.bg-dark .login-box {
  background: rgba(25, 36, 50, .7);
  box-shadow: 0 15px 25px rgba(25, 36, 50, .6);
}

body.bg-dark .login-box .user-box input {
  color: #a2b9c7;
  border-bottom: 1px solid #192432;
  transition: border-bottom-color .3s ease-out;
}

body.bg-dark .login-box .user-box input:focus, body.bg-dark .login-box .user-box input:valid {
  border-bottom-color: #a2b9c7;
  transition: border-bottom-color .3s ease-in;
}

body.bg-dark .login-box .user-box label {
  color: #a2b9c7;
}

body.bg-light .login-box {
  background: rgba(162, 185, 199, .7);
  box-shadow: 0 15px 25px rgba(162, 185, 199, .6);
}

body.bg-light .login-box .user-box input {
  color: #192432;
  border-bottom: 1px solid #a2b9c7;
  transition: border-bottom-color .3s ease-out;
}

body.bg-light .login-box .user-box input:focus, body.bg-light .login-box .user-box input:valid {
  border-bottom-color: #192432;
  transition: border-bottom-color .3s ease-in;
}

body.bg-light .login-box .user-box label {
  color: #192432;
}

body main {
  background-image: url("sign_in.a86660f7.svg");
  background-size: cover;
}

body main .login-box {
  box-sizing: border-box;
  border-radius: 10px;
  width: 400px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

body main .login-box .user-box {
  position: relative;
}

body main .login-box .user-box input {
  background: none;
  border: none;
  outline: none;
  width: 100%;
  margin-bottom: 2rem;
  padding: 10px 0;
  font-size: 1rem;
}

body main .login-box .user-box input:focus + label, body main .login-box .user-box input:valid + label {
  font-size: 12px;
  top: -20px;
  left: 0;
}

body main .login-box .user-box label {
  pointer-events: none;
  padding: 10px 0;
  font-size: 1rem;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: 0;
}

